@media(max-width:768px){.hideOnMobile{display:none}}@media(max-width:1024px){.hideOnTablet{display:none}}@media(max-width:768px){.hideOnTablet{display:inherit}}.CenterPane{height:100%;display:flex;justify-content:center;padding:1rem;width:100%}@media(max-width:768px){.CenterPane{padding:0}}.ContentFormContainer{width:100%;max-width:48rem}@media(max-width:768px){.ContentFormContainer{padding:1rem}}@media(max-width:640px){.ContentFormContainer{padding:.25rem}}.ContentFormContainer__FormAndColumns{position:relative;padding:.5rem 2rem;margin:.25rem;border-radius:.25rem;--bg-opacity:1;background-color:#fff;background-color:rgba(255,255,255,var(--bg-opacity));box-shadow:0 1px 3px 0 rgba(0,0,0,.1),0 1px 2px 0 rgba(0,0,0,.06)}@media(max-width:640px){.ContentFormContainer__FormAndColumns{padding-left:.75rem;padding-right:.75rem}}.ContentFormContainer.isDiff{max-width:none}.ContentFormContainer.isDiff .ContentFormContainer__FormAndColumns{padding:0;background-color:transparent;border-style:none;box-shadow:none}.ContentFormContainer__DecorationBoxesColumn{position:absolute;top:0;z-index:30;width:300px;right:-315px}