.FullCalendar{margin-top:1em}.FullCalendar__calendar{width:100%}.FullCalendar .fc-view-container{background-color:#fff}.FullCalendar__backdrop{background-color:hsla(0,0%,98%,.89);position:absolute;z-index:40;width:100%;height:100%;display:flex;justify-content:center}.popOverEvent{display:flex;flex-direction:column;padding:1em;align-items:flex-start}.popOverEvent__title{font-weight:bolder;color:#1f2d3d;font-size:1.15em;margin-bottom:.5em}.popOverEvent__title__task{text-align:left}.popOverEvent__title__item{color:#738a95}