@media(max-width:768px){.hideOnMobile{display:none}}@media(max-width:1024px){.hideOnTablet{display:none}}@media(max-width:768px){.hideOnTablet{display:inherit}}.OffPanel__container{display:flex;flex-direction:column;padding:1rem;background:#fff;position:fixed;z-index:90}@media(max-width:768px){.OffPanel__container{width:100%!important}}.OffPanel__container.stretched{padding:0}.OffPanel__container.bottom,.OffPanel__container.top{width:100%;right:0;left:0;overflow-x:auto}.OffPanel__container.top{top:0}.OffPanel__container.bottom{bottom:0}.OffPanel__container.left,.OffPanel__container.right{height:100%;top:0;bottom:0;overflow-y:auto}.OffPanel__container.right{right:0}.OffPanel__container.left{left:0}.OffPanel__header{display:flex;justify-content:space-between;align-items:center;flex-shrink:0;margin-bottom:1rem}.OffPanel__header.stretched{padding:1rem 1rem 0}.OffPanel__header__title{font-size:1.125rem;font-weight:900;color:#000;line-height:1.25}.OffPanel__body{flex:1 1 auto}.OffPanel__backdrop{background-color:rgba(12,12,12,.33);display:block;position:fixed;bottom:0;left:0;right:0;top:0;z-index:89}