.BigFilter{display:flex;flex-wrap:wrap;align-items:center;width:100%}.BigFilter__filterItem{justify-content:space-between;align-content:center;font-size:.75rem;border-radius:.125rem;max-width:20rem;--bg-opacity:1;background-color:#05505c;background-color:rgba(5,80,92,var(--bg-opacity));cursor:pointer;margin-bottom:.25rem;margin-right:.25rem;max-width:20rem}.BigFilter__filterItem,.BigFilter__filterItemDescription{display:flex;align-items:center;height:37px;line-height:37px}.BigFilter__filterItemDescription{font-weight:600;padding-left:.5rem;padding-right:.25rem;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.BigFilter__filterItemDescription:focus,.BigFilter__filterItemDescription:hover{--bg-opacity:1;background-color:#036672;background-color:rgba(3,102,114,var(--bg-opacity))}.BigFilter__removeFilter{display:flex;justify-content:center;align-items:center;border-top-right-radius:.25rem;border-bottom-right-radius:.25rem;--bg-opacity:1;background-color:#05505c;background-color:rgba(5,80,92,var(--bg-opacity));padding:.25rem;height:37px;line-height:37px}.BigFilter__removeFilter:focus,.BigFilter__removeFilter:hover{--bg-opacity:1;background-color:#036672;background-color:rgba(3,102,114,var(--bg-opacity))}.BigFilter__searchInput{flex-grow:1;border-radius:.125rem;--bg-opacity:1;background-color:#fff;background-color:rgba(255,255,255,var(--bg-opacity));box-shadow:0 1px 3px 0 rgba(0,0,0,.1),0 1px 2px 0 rgba(0,0,0,.06);width:14rem;padding-left:.5rem;padding-right:2rem;height:37px;line-height:37px}.BigFilter__searchClear{position:absolute;display:inline-block;cursor:pointer;color:#374151;right:0;top:.25em}.BigFilter__searchClear:hover{color:#000}@media(max-height:550px){.BigFilter__filteringDropdown,.BigFilter__searchHintDropdown{max-height:250px}}@media(min-height:550px)and (max-height:650px){.BigFilter__filteringDropdown,.BigFilter__searchHintDropdown{max-height:350px}}@media(min-height:650px)and (max-height:750px){.BigFilter__filteringDropdown,.BigFilter__searchHintDropdown{max-height:450px}}@media(min-height:750px){.BigFilter__filteringDropdown,.BigFilter__searchHintDropdown{max-height:550px}}.BigFilter__searchHintDropdown{position:absolute;width:16rem;--bg-opacity:1;background-color:#fff;background-color:rgba(255,255,255,var(--bg-opacity));box-shadow:0 1px 3px 0 rgba(0,0,0,.1),0 1px 2px 0 rgba(0,0,0,.06);color:#4b5563;padding:.75rem;top:100%;z-index:30}.BigFilter__filteringDropdown{padding-right:1rem;overflow-x:hidden;overflow-y:auto;min-width:300px}