.UserMessages__message{display:flex;justify-content:space-between;align-items:center;width:100%;--bg-opacity:1;background-color:#6c2bd9;background-color:rgba(108,43,217,var(--bg-opacity));color:#edebfe;padding:.5rem}.UserMessages__message.impersonate{justify-content:center}.UserMessages__message a{color:#7edce2;text-decoration:underline}@-webkit-keyframes blinker{50%{opacity:0}}@keyframes blinker{50%{opacity:0}}.UserMessages__message.twinkle{-webkit-animation:blinker 1s linear infinite;animation:blinker 1s linear infinite}