@media(max-width:768px){.hideOnMobile{display:none}}@media(max-width:1024px){.hideOnTablet{display:none}}@media(max-width:768px){.hideOnTablet{display:inherit}}.ItemListElement{display:flex;--bg-opacity:1;background-color:#fff;background-color:rgba(255,255,255,var(--bg-opacity));border-color:#e5e7eb;border-width:1px 1px 0;flex-direction:row;align-items:center;justify-content:space-between;min-height:2.7rem;width:100%}.ItemListElement:hover{background-color:#fafafa}.ItemListElement:last-child{border-bottom-width:1px}@media(max-width:768px){.ItemListElement{border-radius:.125rem;border-width:1px;border-color:#e5e7eb;border-radius:.25rem}.ItemListElement:hover{--bg-opacity:1;background-color:#fff;background-color:rgba(255,255,255,var(--bg-opacity))}}