@media(max-width:768px){.hideOnMobile{display:none}}@media(max-width:1024px){.hideOnTablet{display:none}}@media(max-width:768px){.hideOnTablet{display:inherit}}.AnnotationElement{display:block;width:100%;border:1px solid #cecece;margin-bottom:10px;background:#fff;border-radius:3px;box-shadow:1px 1px 2px rgba(0,0,0,.05);z-index:30}.AnnotationElement__close,.AnnotationElement a{font-size:.8em;cursor:pointer;text-decoration:none}.AnnotationElement__close{padding:6px;float:right;color:#757575}.AnnotationElement__resolve{padding:6px;border-bottom:1px solid #f1f3f4;font-size:.8em;color:#757575}.AnnotationElement__resolve input{margin:0}.AnnotationElement__resolve span{cursor:pointer}.AnnotationElement__resolve span:hover{color:#1a56db}.AnnotationElement__confirmResolve{background-color:#f2f6fa;padding:5px;margin:5px}.AnnotationElement__confirmResolve a{background:#fff;padding:3px;margin-right:3px;margin-top:3px;font-size:1.1em}.AnnotationElement__selectedText{border-bottom:1px solid #cecece;padding:10px;color:#9e9e9e;font-style:italic;font-size:.9em;quotes:"" "";max-height:45px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.AnnotationElement__selectedText:before{content:open-quote}.AnnotationElement__selectedText:after{content:close-quote}.AnnotationElement__selectedText:hover{max-height:none;white-space:normal}.AnnotationElement__avatar{width:26px;height:auto;border-radius:50%}.AnnotationElement__newComment{padding:10px;background-color:#f6f9fc;border-top:1px solid #ececec}.AnnotationElement__newComment a:first-of-type{margin-left:33px}.AnnotationElement__newComment textarea,.AnnotationElement__newComment textarea:focus{border:none;background:none;box-shadow:none;outline:none!important;margin-bottom:0;width:calc(100% - 31px);vertical-align:top;display:inline-block;padding:3px 0 0 3px}.AnnotationElement__comments{padding:10px}.AnnotationElement__comments .buttons{text-align:center}.AnnotationElement__comments textarea{border:none;background:#f1f3f4;width:100%!important;margin-top:5px;padding:8px}.AnnotationElement__comment{padding:5px}.AnnotationElement__user{display:inline-block;vertical-align:top}.AnnotationElement__timeAgo{font-size:.8em;color:#9e9e9e}.AnnotationElement__commentActions{display:inline-block;margin:2px 0 0 2px}.AnnotationElement__commentActions .AnnotationElement__user{font-size:.9em}.AnnotationElement__commentActions a{display:inline-block;margin-left:5px;cursor:pointer;float:right}.AnnotationElement__commentContent{display:block;font-size:1em;word-wrap:break-word;width:100%;padding-left:31px}