.RichTextImagePrompt{height:500px;padding:2rem;overflow-y:auto}.RichTextImagePrompt__linkedAssets{display:flex;flex-wrap:wrap}.RichTextImagePrompt__linkedAssets a{border-width:2px;margin-right:1rem;overflow:hidden;width:100px}.RichTextImagePrompt__linkedAssets a img{max-width:100%;max-height:100px;-o-object-fit:contain;object-fit:contain;-o-object-position:center top;object-position:center top}.RichTextImagePrompt__linkedAssets a.selected{border-color:#f44336}.RichTextImagePrompt__linkedAssets img{width:100%}