@media(max-width:768px){.hideOnMobile{display:none}}@media(max-width:1024px){.hideOnTablet{display:none}}@media(max-width:768px){.hideOnTablet{display:inherit}}.AnnotatableImage__hintMsg,.AnnotatableImage__viewCanvas{-moz-transition-property:opacity;-moz-transition-duration:.5s;-moz-transition-delay:0s;-webkit-transition-property:opacity;-webkit-transition-duration:.5s;-webkit-transition-delay:0s;-o-transition-property:opacity;-o-transition-duration:.5s;-o-transition-delay:0s;transition-property:opacity;transition-duration:.5s;transition-delay:0s}.AnnotatableImage__annotationLayer{display:inline-block;position:relative}.AnnotatableImage__annotationLayer.editable{cursor:crosshair}.AnnotatableImage__img{max-width:100%;width:100%;line-height:0;border-radius:0;height:auto}.AnnotatableImage canvas{position:absolute;top:0;left:0;z-index:10}.AnnotatableImage__viewCanvas.focus{opacity:1}.AnnotatableImage__viewCanvas.unfocus{opacity:.4}.AnnotatableImage__Annotation{padding:5px;width:300px;cursor:default;font-size:17px}.AnnotatableImage__hint{position:absolute;white-space:nowrap;pointer-events:none}.AnnotatableImage__hintMsg{background-color:rgba(0,0,0,.5);margin:4px;padding:8px 15px 8px 30px;font-family:lucida grande,tahoma,verdana,arial,sans-serif;line-height:normal;font-size:12px;color:#fff;border-radius:4px;-moz-border-radius:4px;-webkit-border-radius:4px;-khtml-border-radius:4px}.AnnotatableImage__hintIcon{position:absolute;top:6px;left:5px;background:url(/static/feather_icon.png?59e3a4a5e3d7406b3ebae64ad31a4225);background-repeat:no-repeat;width:19px;height:22px;margin:2px 4px 0 6px}